body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.qf-layout{position:absolute;top:0;bottom:0;left:0;right:0;overflow:hidden}.qf-layout .ant-layout{height:100%}.qf-layout .qf-sider{position:absolute;top:0;bottom:0;left:0;width:150px;background:#001529;overflow:auto}.qf-layout .qf-sider>div{text-align:center;line-height:40px;cursor:pointer;border-bottom:1px solid #aaa}.qf-layout .qf-sider>div>a{text-decoration:none;display:block;width:100%;height:100%;color:white}.qf-layout .qf-sider>div>a.on{background:orange}.qf-layout .qf-header{position:absolute;top:0;height:60px;left:150px;right:0;padding:15px}.qf-layout .qf-main{position:absolute;top:60px;bottom:0;left:150px;right:0;background-color:#eee;padding:15px}.qf-layout .qf-main>div{position:absolute;top:15px;left:15px;right:15px;bottom:15px;overflow:auto;background:white;padding:15px;border-radius:5px;padding-bottom:100px;min-width:750px}

